import Vue from 'vue'
import Router from 'vue-router'

import PageDashboard from '../../pages/dashboard/route'
import PageSubAgents from '../../pages/sub-agents/route'
import PagePlayers from '../../pages/players/route'
import PageBet from '../../pages/bet/route'
import PageAgentCredits from '../../pages/agent-credits/route'
import PageCreditLedger from '../../pages/credit-ledger/route'
import PageWinners from '../../pages/winners/route'
import PageDraws from '../../pages/draws/route'
import PageCommission from '../../pages/commission/route'
import PageWinningShares from '../../pages/winning-shares/route'

import Config from '../../config'
import Utils from '../../utils'

Vue.use(Router)

let auth = localStorage.getItem(Config.info.AUTH_KEY)
let routes = [
  {
    path: '/',
    redirect: '/dashboard'
  },
  PageDashboard,
  PageSubAgents,
  PagePlayers,
  PageBet,
  PageAgentCredits,
  PageCreditLedger,
  PageWinners,
  PageDraws,
  PageCommission,
  PageWinningShares
]

if (auth) {
  let { user } = Utils.Auth.decrypt(auth)

  // Remove PageSubAgents for non agent users
  if (   user
      && user.agent_type != 'agent') {
    routes.splice(2, 1)
    routes.splice(4, 1)
  }
}

let router = new Router({ routes })
router.beforeEach((to, from, next) => {
  if (to.name) {
    next()
  } else {
    next(new Error('Page not accessible'))
  }
})

export default {
  routes,
  router
}


