import Api from '../../api'
import Config from '../../config'

export default {
  namespaced: true,

  actions: {
    async list(context, params) {
      let { data } = await Api.request({
        url: `${ Config.info.API_LOAD_AGENT }`,
        params
      })

      return data
    },

    async get(context, id) {
      let { data } = await Api.request({
        url: `${ Config.info.API_LOAD_AGENT }/${ id }`
      })

      return data
    },

    async save(context, data) {
      await Api.request({
        method: data.id ? 'PUT' : 'POST',
        url: `${ Config.info.API_LOAD_AGENT }${ data.id ? `/${ data.id }` : '' }`,
        data
      })
    },

    async remove(context, id) {
      await Api.request({
        method: 'DELETE',
        url: `${ Config.info.API_LOAD_AGENT }/${ id }`
      })
    },

    async search(context, name) {
      let { data } = await Api.request({
        method: 'POST',
        data: {
          sub_agent: name
        },
        url: Config.info.API_LOAD_SEARCH_AGENT
      })

      return data
    }
  }
}


