import Vue from 'vue'
import Elementlang from 'element-ui/lib/locale/lang/en'
import ElementLocale from 'element-ui/lib/locale'
import ElementUI from 'element-ui'

import '../../assets/style/element-variables.scss'
import '../../directives/date-format'
import '../../directives/money-format'
import '../../directives/time-format'

// Components
import Navbar from '../../components/navbar'

import Config from '../../config'

// configure language
ElementLocale.use(Elementlang)

// Global components
Vue.use(ElementUI)

// Default element-ui component size
Vue.prototype.$ELEMENT = { size: 'small' }

export default {
  name: 'Main',

  components: {
    Navbar
  },

  methods: {
    async logout() {
      localStorage.removeItem(Config.info.AUTH_KEY)

      location.reload()
    }
  }
}


