import axios from 'axios'
import Vue from 'vue'
import Vuex from 'vuex'

Vue.config.productionTip = false
Vue.use(Vuex)

async function startApp() {
  // Load hosted config.json
  const Config = (await axios.get(`/static/config.json?ref=${ new Date().getTime() }`)).data
  require('./config').default.init(Config)

  // App for auth
  const Auth = require('./apps/auth').default

  // App for Main
  const Main = require('./apps/main').default
  const MainRouter = require('./apps/main/router').default

  const Api = require('./api').default
  const Utils = require('./utils').default

  let auth = localStorage.getItem(Config.AUTH_KEY)

  if (auth) {
    let { user, access_token } = Utils.Auth.decrypt(auth)

    if (user && access_token) {
      new Vue({
        el: '#app',

        router: MainRouter.router,

        async created() {
          Api.init(access_token)
          require('./store')
          this.$store.commit('SET_USER', user)
        },

        render(h) {
          return h(Main)
        }
      })
    }
  } else {
    new Vue({
      el: '#app',

      created() {
        Api.init()
        require('./store')
      },

      render(h) {
        return h(Auth)
      }
    })
  }
}

startApp()



