import Vue from 'vue'
import Elementlang from 'element-ui/lib/locale/lang/en'
import ElementLocale from 'element-ui/lib/locale'
import ElementUI from 'element-ui'

import '../../assets/style/element-variables.scss'

// configure language
ElementLocale.use(Elementlang)

// Global components
Vue.use(ElementUI)

export default {
  name: 'Auth',

  data() {
    return {
      loading: false,
      twofactor: false,
      tfAuth: {
        digit_code: null,
      },
      user: {
        username: '',
        password: ''
      },
      rules: {
        username: [
          {
            required: true,
            message: 'Please input username',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: 'Please input password',
            trigger: 'blur'
          }
        ]
      },
      tfRules: {
        digit_code: [
          {
            required: true,
            message: 'Please input digit code',
            trigger: 'blur'
          },
          {
            validator(rule, value, callback) {
              if (value.length < 6) {
                callback(new Error('Digit code is invalid'))
              } else {
                callback()
              }
            },
            trigger: 'change'
          }
        ]
      },
      auth: null
    }
  },

  methods: {
    handleError(e) {
      let type = 'error'
      let { response } = e

      if (response) {
        let { data } = response

        if (   data
            && data.message) {
          if (_.isArray(data.message)) {
            this.handleMessages(data.message, type)
          } else if (_.isString(data.message)) {
            this.handleMessage({
              message: data.message,
              type
            })
          }
        } else {
          this.handleMessage({
            message: e.message,
            type
          })
        }
      } else {
        this.handleMessage({
          message: e.message,
          type
        })
      }
    },

    async handleMessage({ message, type }) {
      return new Promise((resolve) => {
        setTimeout(() => {
          this.$message({
            duration: 5000,
            message,
            type
          })

          resolve()
        }, 100)
      })
    },

    async handleMessages(messages, type) {
      let index = 0

      while (messages[index]) {
        let message = messages[index]
        await this.handleMessage({ message, type })
        index++
      }
    },

    async login() {
      this.$refs.loginForm
        .validate(async valid => {
          if (!valid || this.loading) {
            return
          }

          this.loading = true

          try {
            let auth = await this.$store.dispatch('login', this.user)

            if (auth.twofactor) {
              this.loading = false
              this.auth = auth
            } else {
              await this.$store.dispatch('verified', auth)
            }
          } catch(e) {
            this.loading = false
            this.handleError(e)
          }
        })
    },

    async twoFactorLogin() {
      this.$refs.tfaForm
        .validate(async valid => {
          if (!valid || this.loading) {
            return
          }

          this.loading = true

          try {
            let params = {
              twofactorcode: this.tfAuth.digit_code,
              token: this.auth.access_token
            }
            await this.$store.dispatch('verify', params)
            await this.$store.dispatch('verified', this.auth)
          } catch(e) {
            this.loading = false
            this.handleError(e)
          }
        })
    },

    async resend() {
      if (this.loading) return

      this.loading = true

      try {
        await this.$store.dispatch('resend', this.auth.access_token)
        this.$message({
          message: `Resend successful.`,
          type: 'success'
        })
      } catch(e) {
        this.handleError(e)
      }

      this.loading = false
    },

    async cancel(type) {
      if (type == 'twofactor') {
        this.user = {
          username: '',
          password: ''
        }
        this.auth = null
      }
    }
  }
}


