import Vue from 'vue'
import Vuex from 'vuex'
import Api from '../api'

import Config from '../config'
import Utils from '../utils'

// Modules
import agent_credits from './modules/agent_credits'
import bet_history from './modules/bet_history'
import bet from './modules/bet'
import commission from './modules/commission'
import credit_ledger from './modules/credit_ledger'
import dashboard from './modules/dashboard'
import draws from './modules/draws'
import player from './modules/player'
import subagent from './modules/subagent'
import winners from './modules/winners'
import winning_shares from './modules/winning_shares'

export const store = new Vuex.Store({
  modules: {
    agent_credits,
    bet_history,
    bet,
    commission,
    credit_ledger,
    dashboard,
    draws,
    player,
    subagent,
    winners,
    winning_shares
  },

  state: {
    token: null,
    user: null
  },

  getters: {
    token(state) {
      return state.token
    },

    user(state) {
      return state.user
    }
  },

  mutations: {
    SET_TOKEN(state, token) {
      state.token = token
    },

    SET_USER(state, user) {
      state.user = user
    }
  },

  actions: {
    async login({ commit }, user) {
      let { data }  = await Api.request({
        method: 'POST',
        url: Config.info.API_LOGIN,
        data: user
      })

      return data
    },

    async verify({ commit }, { twofactorcode, token }) {
      let { data }  = await Api.request({
        method: 'POST',
        headers: {
          Authorization: `Bearer ${ token }`
        },
        url: Config.info.API_AUTH_VERIFY,
        params: {
          twofactorcode
        }
      })

      return data
    },

    async resend({ commit }, token) {
      let { data }  = await Api.request({
        method: 'GET',
        headers: {
          Authorization: `Bearer ${ token }`
        },
        url: Config.info.API_AUTH_RESEND
      })

      return data
    },

    async verified({ commit }, data) {
      let item = Utils.Auth.encrypt(data)

      localStorage.setItem(Config.info.AUTH_KEY, item)
      location.href = ''
    },

    async logout({ commit }) {
      try {
        await Api.request({
          url: Config.info.API_LOGOUT
        })
      } catch(e) {}

      commit('SET_TOKEN', null)
      commit('SET_USER', null)

      localStorage.removeItem(Config.info.AUTH_KEY)
      location.href = ''
    },

    async server() {
      let { data } = await Api.request({ url: Config.info.API_SERVERTIME })
      return data
    }
  }
})

Vue.prototype.$store = store


