import Api from '../../api'
import Config from '../../config'

export default {
  namespaced: true,

  actions: {
    async list(context, params) {
      let { data } = await Api.request({
        url: Config.info.API_CREDIT_LEDGER,
        params
      })

      return data
    }
  }
}


