import Api from '../../api'
import Config  from '../../config'

const API_ENTITY = Config.info.API_SUB_AGENT
const API_SEARCH = Config.info.API_SEARCH_AGENT

export default {
  namespaced: true,

  actions: {
    async list(context, params) {
      let { data } = await Api.request({
        url: API_ENTITY,
        params
      })

      return data
    },

    async get(context, id) {
      let { data } = await Api.request({
        url: `${ API_ENTITY }/${ id }`
      })

      return data[0]
    },

    async remove(context, id) {
      await Api.request({
        method: 'DELETE',
        url: `${ API_ENTITY }/${ id }`
      })
    },

    async save(context, data) {
      await Api.request({
        method: data.id ? 'PUT' : 'POST',
        url: `${ API_ENTITY }${ data.id ? `/${ data.id }` : '' }`,
        data
      })
    },

    async search(context, name) {
      let { data } = await Api.request({
        method: 'POST',
        data: {
          sub_agent: name
        },
        url: API_SEARCH
      })

      return data
    }
  }
}


