import Api from '../../api'
import Config from '../../config'

export default {
  namespaced: true,

  actions: {
    async list(context, params) {
      let { data } = await Api.request({
        url: Config.info.API_BET_HISTORY,
        params
      })

      return data
    },

    async void(context, id) {
      return await Api.request({ url: `${ Config.info.API_BET_VOID }/${ id }`})
    }
  }
}


