import pages from './pages'

import Auth from '../../mixins/auth'

export default {
  name: 'Navbar',

  mixins: [
    Auth
  ],

  data() {
    return {
      defaultActive: null,
      hideMenu: false,
      pages
    }
  },

  computed: {
    fullName() {
      return this.$user ? this.$user.username : ''
    }
  },

  watch: {
    $route() {
      this.defaultActive = this.$route.path
    }
  },

  methods: {
    async logout() {
      await this.$store.dispatch('logout')

      location.href = ''
    }
  }
}



