import Api from '../../api'
import Config from '../../config'

export default {
  namespaced: true,

  actions: {
    async list() {
      let { data } = await Api.request({
        url: Config.info.API_DRAWLISTS
      })

      return data
    },

    async send(context, data) {
      let options = {
        method: 'POST',
        url: Config.info.API_DRAW_TRANSACTION,
        data
      }

      return await Api.request(options)
    },
  }
}


