import Vue from 'vue'
import moment from 'moment'

Vue.directive('date-format', (el, binding) => {
  let value = binding.value

  if (   value
      && value.date) {
    if (value.format) {
      el.innerHTML = moment(value.date).format(value.format)
    } else {
      el.innerHTML = moment(value.date)
    }
  }
})



