import Api from '../../api'
import Config  from '../../config'

const API_ENTITY = Config.info.API_WINNERS

export default {
  namespaced: true,

  actions: {
    async list(context, params) {
      let { data } = await Api.request({
        url: API_ENTITY,
        params
      })

      return data
    },

    async claim(context, id) {
      return await Api.request({
        method: 'PUT',
        url: `${ API_ENTITY }/${ id }`
      })
    }
  }
}


