import _ from 'lodash'
import crypto from 'crypto-js'

const HASH = 'MediaOne'

export default {
  name: 'Auth',

  encrypt(data) {
    let message = typeof data == 'object' ?
      JSON.stringify(data) : '' + data
    let value = crypto.AES.encrypt(message, HASH).toString()

    return value
  },

  decrypt(message) {
    let data = crypto.AES.decrypt(message, HASH).toString(crypto.enc.Utf8)

    try {
      return JSON.parse(data)
    } catch(e) {}

    return null
  }
}


