import Vue from 'vue'

Vue.directive('money-format', (el, binding) => {
  let value = binding.value

  if (   value != undefined
      || value != null) {
    let moneyOpt = { style: 'currency', currency: 'PHP' }
    let moneyFormat = new Intl.NumberFormat('en-US', moneyOpt)

    el.innerHTML = moneyFormat.format(value)
  }
})



