import Vue from 'vue'
import moment from 'moment'

Vue.directive('time-format', (el, binding) => {
  let value = binding.value

  if (   value
      && value.time) {
    let tpl = value.tpl || 'HH:mm'

    if (value.format) {
      el.innerHTML = moment(value.time, tpl).format(value.format)
    } else {
      el.innerHTML = moment(value.time, tpl)
    }
  }
})



