import Api from '../../api'
import Config from '../../config'

export default {
  namespaced: true,

  actions: {
    async list() {
      let { data } = await Api.request({
        url: Config.info.API_DRAWS
      })

      return data
    },

    async get() {
      let { data } = await Api.request({
        url: Config.info.API_DRAW_DROPDOWN
      })

      return data
    }
  }
}


