export default {
  name: 'Cookies',

  clear() {
    let res = document.cookie
    let multiple = res.split(';')

    for (var i = 0; i < multiple.length; i++) {
      let key = multiple[i].split('=')
      document.cookie = key[0] + ' =; expires = Thu, 01 Jan 1970 00:00:00 UTC'
    }
  }
}


